import React, { useState, useEffect, useRef } from 'react';
import { IconButton } from '@material-ui/core';
import CameraAltIcon from '@material-ui/icons/CameraAlt';
import FlipCameraIosIcon from '@material-ui/icons/FlipCameraIos';
import { Template } from './templates/Template';
import Webcam from 'react-webcam';
import { Flip } from '@material-ui/icons';
import { setBackend } from '@tensorflow/tfjs-core';

const UseWindowDimensions = () => {
    const getWindowDimensions = () => {
        const { innerWidth: width, innerHeight: height } = window;
        return { width, height }
    }
    const [windowDimensions, setWindowDimensions] = useState(getWindowDimensions());
    useEffect(() => {
        const onResize = () => {
            setWindowDimensions(getWindowDimensions());
        }
        window.addEventListener('resize', onResize);
        return () => window.removeEventListener('resize', onResize);
    }, []);
    return windowDimensions;
}

interface ButtonsProps {
    
    isCaptured: boolean
    setCaptured: Function
    facingMode: string
    setFacingMode: Function
    shutter: boolean
    setShutter: Function
}

const Buttons: React.FC<ButtonsProps> = ({
    isCaptured,
    setCaptured,
    facingMode,
    setFacingMode,
    shutter,
    setShutter,
}) => {
    return (
        <div
            style={{
                display: 'flex',
                justifyContent: 'space-around',
                alignItems: 'center'
            }}
        >
            <IconButton>
                <CameraAltIcon 
                    color='primary' 
                    onClick={() => {
                        if (!isCaptured) {
                            setShutter(true);
                        } else {
                            setCaptured(false);
                        }
                    }}
                />
            </IconButton>
            <IconButton disabled={isCaptured}>
                <FlipCameraIosIcon
                    color={isCaptured ? 'disabled' : 'primary'}
                    onClick={() => {
                        if (facingMode === 'user') {
                            setFacingMode('environment');
                        } else if (facingMode === 'environment') {
                            setFacingMode('user');
                        }
                    }}
                />
            </IconButton>
        </div>
    );
}

interface CanvasProps {
    webcamRef: any
    isCaptured: boolean
    setCaptured: Function
    facingMode: string
    shutter: boolean
    setShutter: Function
    background: HTMLImageElement
    setBackground: Function
    width: number
    height: number
}

const Canvas: React.FC<CanvasProps> = ({
    webcamRef,
    isCaptured,
    setCaptured,
    facingMode,
    shutter,
    setShutter,
    background,
    setBackground,
    width,
    height
}) => {
    const canvasRef = useRef<HTMLCanvasElement>(null);
    const GetScreenshot = async () => {
        if (webcamRef.current) {
            const webcamCurrent = webcamRef.current as any;
            console.log('webcam ready.')
            if (webcamCurrent.video.readyState === 4) {
                const imgSrc = webcamRef.current?.getScreenshot();
                let img = new Image;
                img.src = imgSrc;
                img.onload = () => {
                    setBackground(img);
                    setShutter(false);
                    setCaptured(true);
                }
            }
        }
    }
    const DrawCanvas = async () => {
        if (isCaptured) {
            if (canvasRef.current) {
                canvasRef.current.width = background.naturalWidth;
                canvasRef.current.height = background.naturalHeight;
                const context = canvasRef.current.getContext('2d');
                context?.drawImage(background, 0, 0);
            }
        }
    }

    useEffect(() => {
        GetScreenshot();
    }, [shutter]);

    useEffect(() => {
        DrawCanvas();
    }, [isCaptured]);

    return (
        <div
            style={{
                width: width,
                height: height*0.9
            }}
        >
            {!isCaptured &&
                <Webcam
                    audio={false}
                    ref={webcamRef}
                    videoConstraints={{
                        facingMode: facingMode
                    }}
                    style={{
                        margin: 'auto',
                        textAlign: 'center',
                        zIndex: 9,
                        width: '100%',
                        height: '100%'
                    }}
                />
            }
            {isCaptured &&
                <>
                    <canvas
                        ref={canvasRef}
                        style={{
                            margin: 'auto',
                            width: '100%',
                            height: '100%',
                            zIndex: 30
                        }}
                    />
                </>
            }
        </div>
    );
}

interface SettingsProps {
    background: HTMLImageElement
    setBackground: Function
}

export const Settings: React.FC<SettingsProps> = ({
    background,
    setBackground
}) => {
    const { width, height } = UseWindowDimensions();
    const [isCaptured, setCaptured] = useState<boolean>(false);
    const [facingMode, setFacingMode] = useState<string>('user');
    const [shutter, setShutter] = useState<boolean>(false);
    const webcamRef = useRef<Webcam>(null);
    return (
        <Template title='settings'>
            <Buttons 
                isCaptured={isCaptured}
                setCaptured={setCaptured}
                facingMode={facingMode}
                setFacingMode={setFacingMode}
                shutter={shutter}
                setShutter={setShutter}
            />
            <Canvas
                webcamRef={webcamRef}
                isCaptured={isCaptured}
                setCaptured={setCaptured}
                facingMode={facingMode}
                shutter={shutter}
                setShutter={setShutter}
                background={background}
                setBackground={setBackground}
                width={width}
                height={height}
            />
        </Template>
    )
}